.popup {
  z-index: 999;
  width: 100vw;
  height: 100vh;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.popup__container {
  max-width: 952px;
  height: auto;
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

@media (min-width: 767px) and (max-width: 1279px) {
  .popup__container {
    max-width: 682px;
  }
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__container {
    max-width: 270px;
  }
}

.popup__button {
  margin-right: 10px;
}

@media (min-width: 767px) and (max-width: 1279px) {
  .popup__button {
    margin-right: 9px;
  }
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__button {
    margin-right: 0;
  }
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__column:nth-child(1) {
    display: none;
  }
}

.popup__content {
  background-color: #fff;
  border-radius: 9px;
  margin-right: 52px;
  display: flex;
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__content {
    margin-right: 30px;
  }
}

.popup__image {
  width: 500px;
  height: 500px;
  border-top-left-radius: 9px;
  border-bottom-left-radius: 9px;
}

@media (min-width: 767px) and (max-width: 1279px) {
  .popup__image {
    width: 350px;
    height: 350px;
  }
}

.popup__column:nth-child(2) {
  margin: 50px 20px 0 29px;
}

@media (min-width: 767px) and (max-width: 1279px) {
  .popup__column:nth-child(2) {
    max-width: 260px;
    margin: 10px 9px 0 11px;
  }
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__column:nth-child(2) {
    max-width: 240px;
    margin: 10px;
  }
}

.popup__title {
  margin-bottom: 10px;
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__title {
    text-align: center;
    font-size: 3.5rem;
    line-height: 130%;
  }
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__subtitle {
    text-align: center;
    font-size: 2rem;
    line-height: 115%;
  }
}

.popup__description {
  margin: 40px 0;
}

@media (min-width: 320px) and (max-width: 1279px) {
  .popup__description {
    margin: 20px 0;
    font-size: 1.3rem;
  }
}

@media (min-width: 320px) and (max-width: 766.999px) {
  .popup__description {
    text-align: justify;
  }
}

.popup__list {
  margin: 0;
  padding: 0;
  list-style: none inside;
}

.popup__item:before {
  content: "â€¢";
  color: #f1cdb3;
  width: 1rem;
  font-weight: bold;
  display: inline-block;
}

.popup__item:nth-child(n+2) {
  margin-top: 10px;
}

@media (min-width: 767px) and (max-width: 1279px) {
  .popup__item:nth-child(n+2) {
    margin-top: 5px;
  }
}

.animation__pop-up-show {
  animation: pop-up-show .3s forwards;
}

.animation__pop-up-close {
  animation: pop-up-close .3s forwards;
}

@keyframes pop-up-show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes pop-up-close {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

