}

.link:hover, .link--active {
  color: #292929;
}

.header__nav-items {
  gap: 35px;
  list-style: none;
  display: flex;
}

.header__nav-items .header__nav-item--active {
  border-bottom: 3px solid #f1cdb3;
}

.header__nav-items .header__nav-item--active > .header__link {
  color: #fafafa;
}

.header__nav-items .header__link {
  color: #cdcdcd;
  transition: color .3s;
}

.header__nav-items .header__link:hover {
  color: #fafafa;
}

.burger {
  width: 30px;
  height: 22px;
  flex-direction: column;
  justify-content: space-between;
  transition: all .3s;
  display: flex;
}

.burger__line {
  height: 2px;
  background-color: #f1cdb3;
  display: block;
}

.burger--color-primary .burger__line {
  background-color: #f1cdb3;
}

.burger--color-secondary .burger__line {
  background-color: #292929;
}

.burger--active {
  transform: rotate(-90deg);
}

.header {
  margin-top: 60px;
}

.header .container {
  z-index: 1;
  justify-content: space-between;
  align-items: center;
  gap: 35px;
  display: flex;
